/* Variables */
:root {
  /* Greys */
  --color-grey-1: #E8E8E8;
  --color-grey-2: #EFEFEF;
  --color-grey-3: #A9A9A9;
  --color-grey-4: #5F5F5F;
  /* General */
  --color-black: #2B2B2B;
  --color-white: #FFFFFF;
  --color-red: #B90000;
  --color-trivia: #226182;
  --color-year:#176152;
  --color-math: #6F6C4D;
  /* Greens */
  --color-green-1: #85C385;
  --color-green-1-dark: #6fb86f;
  --color-green-2: #59AD59;
  --color-green-2-dark: #4B994B;
  /* Elements-related */
  --color-body-bg: var(--color-grey-1);
  --color-main-content-bg: var(--color-white);
  /* Sizes */
  --size-1: .5rem;
  --size-2: calc(2 * var(--size-1));
  --size-3: calc(3 * var(--size-1));
  --size-4: calc(4 * var(--size-1));
  --size-5: calc(5 * var(--size-1));
  --size-6: calc(6 * var(--size-1));
  --root-font-size: 62.5%; /* 1rem = 10px */
  --app-margin-size: var(--size-6);
}

/* Global styles */
* {
  box-sizing: border-box;
}

body,
p,
h1,
h2 {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  font-size: var(--root-font-size);
}

body {
  background: #E8E8E8;
  background: var(--color-body-bg);
  font-family: 'Roboto';
  font-size: 1.6rem;
}
.main-header {
  margin-bottom: var(--size-4);
}

.main-title {
  font-family: 'Ubuntu', sans-serif;
  font-size: 4rem;
  font-weight: 700;
  text-align: center;
}

.main-title__num {
  color: var(--color-trivia);
}

.main-title__in {
  color: var(--color-year);
}

.main-title__fo {
  color: var(--color-math);
}
.main-content-form {
  display: -webkit-flex;
  display: flex;
}
.main-content-form-input {
  border: 0;
  color: var(--color-black);
  font-family: 'Roboto';
  font-size: 2.8rem;
  padding: var(--size-3) var(--size-4);
  width: 100%;
}

.main-content-form-input:focus {
  outline: none;
}

.main-content-form-input::-webkit-input-placeholder {
  color: var(--color-grey-3);
  font-family: 'Ubuntu', sans-serif;
}

.main-content-form-input::-ms-input-placeholder {
  color: var(--color-grey-3);
  font-family: 'Ubuntu', sans-serif;
}

.main-content-form-input::placeholder {
  color: var(--color-grey-3);
  font-family: 'Ubuntu', sans-serif;
}
@-webkit-keyframes mainContentFormButton {
  0% {
    background-color: var(--color-green-1);
    border-color: var(--color-green-1-dark);
  }
  
  50% {
    background-color: var(--color-green-2);
    border-color: var(--color-green-2-dark);
  }

  100% {
    background-color: var(--color-green-1);
    border-color: var(--color-green-1-dark);
  }
}

@keyframes mainContentFormButton {
  0% {
    background-color: var(--color-green-1);
    border-color: var(--color-green-1-dark);
  }
  
  50% {
    background-color: var(--color-green-2);
    border-color: var(--color-green-2-dark);
  }

  100% {
    background-color: var(--color-green-1);
    border-color: var(--color-green-1-dark);
  }
}

.main-content-form-button {
  -webkit-animation: mainContentFormButton 2s linear infinite;
          animation: mainContentFormButton 2s linear infinite;
  border: 0;
  border-bottom: 6px solid;
  color: white;
  cursor: pointer;
  font-family: 'Roboto', sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
  padding: 1rem;
  text-transform: uppercase;
}

.main-content-form-button:hover,
.main-content-form-button:focus {
  outline: none;
}

.main-content-form-button:active {
  border-bottom: 0;
  border-top: 6px solid;
}
.main-content-section {
  background: var(--color-main-content-bg);
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.03);
}

.main-content-section--transparent {
  background: transparent;
  box-shadow: unset;
}

.main-content-section:not(:last-child) {
  margin-bottom: var(--size-4);
}
.main-content-info-box > h2 {
  border-left: 5px solid;
  font-size: 1.8rem;
  font-weight: 700;
  padding: var(--size-2) var(--size-3);
}

.main-content-info-box > p {
  background: rgba(0, 0, 0, 0.05);
  color: var(--color-grey-4);
  line-height: 1.5;
  padding: var(--size-4);
}

/* Trivia */
.main-content-info-box--trivia {
  border-left-color: var(--color-trivia);
}

.main-content-info-box--trivia > h2 {
  color: var(--color-trivia);
}

/* Year */
.main-content-info-box--year {
  border-left-color: var(--color-year);
}

.main-content-info-box--year > h2 {
  color: var(--color-year);
}

/* Math */
.main-content-info-box--math {
  border-left-color: var(--color-math);
}

.main-content-info-box--math > h2 {
  color: var(--color-math);
}

.main-content-message {
  font-size: 1.8rem;
  text-align: center;
}

.main-content-message--error {
  color: var(--color-red);
}

.main-content-message--not-found {
  color: var(--color-grey-4);
}
@-webkit-keyframes loadingSpinner {
  0% {
    fill: var(--color-trivia);
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  50% {
    fill: var(--color-year);
  }

  100% {
    fill: var(--color-math);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes loadingSpinner {
  0% {
    fill: var(--color-trivia);
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  50% {
    fill: var(--color-year);
  }

  100% {
    fill: var(--color-math);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.spinner-icon {
  -webkit-animation-name: loadingSpinner;
          animation-name: loadingSpinner;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-duration: .8s;
          animation-duration: .8s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  height: 3rem;
  width: 3rem;
}
.main-content-loading {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
}
.main-content-center-wrapper {
  margin-top: 2rem;
  text-align: center;
}
.app {
  margin: var(--app-margin-size) auto;
  width: 50%;
}
